#main #modal {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 20;
  display: none;
}

#main #modalClose {
  content: '';
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 20;
  background-color: rgba(0, 0, 0, 0.6);
}

#main #modal .xi-link, #main #modal .xi-call {
  display: inline;
}

#main #modal.on {
  display: block;
}

#main #modal > *:not(#modalClose) {
  display: none;
  width: 80%;
  height: 75%;
  max-width: 700px;
  position: fixed;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #fff;
  border-radius: 5px;
  padding: 0;
}

#main #modal > *:not(#modalClose).on {
  display: block;
}

#main #modal .modal-header {
  background-color: #ec6718;
  justify-content: space-between;
  color: #fff;
  font-size: 20px;
  height: 80px;
}

