  width: -moz-fit-content;
  width: fit-content;
}

#main #modal .modal-header .xi-close {
  cursor: pointer;
}

#main #modal .modal-body {
  overflow: scroll;
  height: calc(100% - 80px);
  padding: 0 40px;
}

#main #modal .pre {
  white-space: pre-wrap;
  padding: 5px 0 20px 10px;
  margin-bottom: 0;
}

#main #modal ul {
  padding-left: 20px !important;
}

#main #modal ul > li:nth-child(1)::marker {
  content: '① ';
}

#main #modal ul > li:nth-child(2)::marker {
  content: '② ';
}

#main #modal ul > li:nth-child(3)::marker {
  content: '③ ';
}

#main #modal ul > li:nth-child(4)::marker {
  content: '④ ';
}

#main #modal ul > li:nth-child(5)::marker {
  content: '⑤ ';
}

#main #modal ul > li:nth-child(6)::marker {
  content: '⑥ ';
}

#main #modal ul > li:nth-child(7)::marker {
  content: '⑦ ';
}

#main #modal ul > li:nth-child(8)::marker {
  content: '⑧ ';
}

