@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap");
.container {
  width: 80%;
  margin: 0 auto;
  padding: 0;
}

@media (max-width: 445px) {
  .container {
    min-width: 300px;
  }
}

.off {
  display: none;
}

.modal-open {
  overflow: hidden;
}

body {
  font-family: 'Noto Sans KR', sans-serif;
}

footer {
  background-color: #f5f5f5;
  font-size: 14px;
  color: #999;
  padding: 40px 0 80px;
}

#footerList {
  padding-bottom: 20px !important;
  border-bottom: 1px solid #666;
}

#footerList li {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 0;
}

#footerList li:after {
  content: '|';
  margin: 5px;
}

#footerList li:last-child::after {
  display: none;
}

#footerList li a {
  font-weight: 600;
  color: #999 !important;
}

#loanInfo {
  margin: 20px 0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  position: relative;
  border-bottom: 1px solid #666;
}

#loanInfo p {
  white-space: pre-wrap;
  font-size: 16px;
  word-break: keep-all;
}

#loanInfo a {
  display: inline-block;
  float: right;
  background-color: #ec6718;
  padding: 5px;
  color: #fff !important;
  border-radius: 5px;
  font-size: 16px;
}

#companyInfo {
  padding-bottom: 20px;
}

#companyInfo .row {
  width: 100%;
  margin: 0 auto;
}

#companyInfo p {
  font-weight: 600;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 0;
  margin: 0;
}

#companyInfo p:after {
  content: '/';
  margin: 5px;
}

#companyInfo p:last-child:after {
  display: none;
}
